import { getCountryByName } from '~/api/countries';
import { json, useLoaderData } from 'superjson-remix';
import { LoaderFunction, useParams } from 'remix';

// Path: app\routes\$cca3.tsx
  export const loader: LoaderFunction = async () => {
    const  cca3  = useParams();
    const response = await fetch(`https://restcountries.eu/rest/v2/alpha/${cca3}`);
    const country = json(await response.json());
    return country;
    };


export default function Country() {
  const country = useLoaderData();

  return (
    <section className='container mx-auto max-w-screen-lg'>
      <div className='grid grid-cols-4 gap-y-12 gap-x-16 mt-11'>
        <div className="bg-white shadow-md shadow-white">
          <img
            className="h-32 w-full"
            src={country.flag}
            alt={`bandeira de ${country.name.common}`}
          />
          <h2 className='my-4'><b>{country.name.common} </b> </h2>
          <p>
            <b>Population:</b> {country.population}
          </p>
          <p>
            <b>Region:</b> {country.region}
          </p>
          <p>
            <b>Capital:</b> {country.capital}
          </p>
        </div>
      </div>
    </section>
  );}